  .navigation {
    margin: 0;
    width: auto;
  }
}
.navigation__wrapper {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 0 auto;
  width: auto;
  min-height: 79px;
}
@media (min-width: 768px) {
  .navigation__wrapper {
    flex-direction: row;
    justify-content: space-between;
