  .navigation__wrapper-top {
    order: 2;
  }
}
.navigation__toggle {
  display: block;
  padding: 0;
  margin-left: auto;
  margin-right: 30px;
  align-self: center;
  width: 21px;
  height: 21px;
  background-color: transparent;
  background-repeat: no-repeat;
  background-size: auto;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}
.navigation--off .navigation__toggle {
  background-image: url("../img/icon-menu-open.svg");
}
.navigation--on .navigation__toggle {
  background-image: url("../img/icon-menu-close.svg");
}
@media (max-width: 767px) {
  .navigation--off .site-nav__item {
    display: none;
  }
}
@media (max-width: 767px) {
  .navigation--off .user-nav__item {
    display: none;
  }
}
@media (max-width: 767px) {
  .navigation--nojs .navigation__toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .navigation--nojs .site-nav__item {
    display: block;
  }
}
@media (max-width: 767px) {
  .navigation--nojs .user-nav__item {
    display: block;
  }
}
.navigation__logo {
  padding: 19px 0 19px 37%;
  align-self: center;
}
@media (min-width: 768px) {
  .navigation__logo {
    padding: 19px 0;
  }
}
