.navigation__logo-tablet {
  display: none;
}
@media (min-width: 768px) {
  .navigation__logo-tablet {
    display: block;
    width: 112px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .navigation__logo-tablet {
    display: none;
  }
}
.navigation__logo-desktop {
  display: none;
}
@media (min-width: 1150px) {
  .navigation__logo-desktop {
    display: block;
    width: 149px;
    height: 72px;
  }
}
.navigation__list {
  margin: 0;
