.radio + .fieldset__label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 40px;
  height: 40px;
  border: 2px solid #444444;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .radio + .fieldset__label::before {
    width: 54px;
    height: 54px;
  }
}
.radio:checked + .fieldset__label::after,
.radio:active + .fieldset__label::after {
  content: "";
  position: absolute;
  left: 12px;
  top: 20%;
  width: 16px;
  height: 16px;
  background-color: #63d1bb;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .radio:checked + .fieldset__label::after,
  .radio:active + .fieldset__label::after {
    top: 30%;
    left: 17px;
    width: 22px;
    height: 22px;
  }
}
.fieldset__label:focus::before,
.fieldset__label:hover::before {
  border: 2px solid #63d1bb;
}
.fieldset__label:active::before {
  border: 2px solid #444444;
  opacity: 0.3;
}
.checkbox + .fieldset__label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 40px;
  height: 40px;
  border: 2px solid #444444;
  background-clip: content-box;
  padding: 10px;
}
@media (min-width: 768px) {
  .checkbox + .fieldset__label::before {
    /*left: 167px;*/
    width: 54px;
    height: 54px;
  }
}
.checkbox:checked + .fieldset__label::after,
.checkbox:active + .fieldset__label::after {
  content: "";
  position: absolute;
  left: 8px;
  top: 20%;
  width: 40px;
  height: 40px;
  background-image: url("../img/icon-tick.svg");
  background-repeat: no-repeat;
  background-size: 23px 17px;
}
@media (min-width: 768px) {
  .checkbox:checked + .fieldset__label::after,
  .checkbox:active + .fieldset__label::after {
    top: 30%;
    background-size: 32px 22px;
  }
}
.fieldset__label:focus::before,
.fieldset__label:hover::before {
  border: 2px solid #63d1bb;
}
.fieldset__label:active::before {
  border: 2px solid #444444;
  opacity: 0.3;
}
